@import url(https://fonts.googleapis.com/css?family=Comfortaa);
:root {
    --black:  #000000;
    --white: #ffffff;
    --purple: #26225e;
    --whitesmoke: #F3F7F9;
    --pink: #fcd8d8;
    --gray: #7f7f7f;
}

* {
    box-sizing: border-box;
    font-family: 'Comfortaa', cursive;
    font-weight: bold;
    margin: 0;
    padding: 0;
}

body {
    background: #F3F7F9;
    background: var(--whitesmoke);
}

button {
    background-color: #ffffff;
    background-color: var(--white);
    border: 2px solid #000000;
    border: 2px solid var(--black);
    border-radius: 8px;
    cursor: pointer;
    font-size: 16px;
    color:#000000;
    color:var(--black);
    margin: 0 2px;
    width: 50px;
    height: 30px;
    outline: none;
}

a {
    color: #000000;
    color: var(--black);
    text-decoration: none;
}

a:hover {
    color: #26225e;
    color: var(--purple);
}

svg {
    display: block;
    margin: 20px auto 0px;
}

img {
    width: 70px;
    height: 70px;
}

h1 {
    display: inline;
}

h2 {
    font-size: 20px;
    text-align: center;
}

.container {
    max-width: 700px;
    margin: 0 auto;
    padding: 20px;
}

.buttons {
    align-items: center;
    display: flex;
    justify-content: center;
    width: 720px;
}

.active {
    background-color: var(--black);
    border: 0;
    color: var(--white);
}

.title {
    align-items: center;
    display: flex;
    justify-content: center;
    margin-top: 120px;
    width: 720px;
}

.info {
    position: relative;
}

.i {
    background-color: var(--pink);
    border-radius: 50%;
    color:var(--black);
    cursor: pointer;
    font-style: italic;
    font-size: 10px;
    font-weight: bold;
    width: 14px;
    height: 14px;
    line-height: 16px;
    margin: 16px 0 0 4px;
    padding-right: 2px;
    text-align: center;
}

p {
    color: var(--gray);
    font-size: 10px;
    font-weight: 100;
    line-height: 12px;
    margin-top: 6px;
}

.note {
    border: 2px solid var(--pink);
    background-color: var(--white);
    border-radius: 8px;
    padding: 0px 6px 6px 6px;
    position: absolute;
    right: 0;
}

.show {
    display: block;
}

.hide {
    display: none;
}

.domain {
    stroke: transparent;
}

.tick line {
    stroke: transparent;
}

.tick text {
    font-family: 'Comfortaa', cursive;
    font-size: 12px;
    font-weight: bold;
}

.link {
    margin-top: 20px;
    width: 720px;
    text-align: right;
}

.logo {
    width: 14px;
    height: 14px;
    margin: 0 2px;
}

